.search {
  display: flex;
  align-items: center;
  width: 250px;
  border: 1px solid #F3F3F3;
  border-radius: 10px;
  padding: 10px 10px;

  img {
    margin-right: 5px;
    max-width: 100%;
  }

  input {
    border: none;
    width: 100%;
  }
}

.search_btn {
  width: 20px;
  height: 15px;
  position: relative;
  border: none;
  outline: none;
}