body {
  margin: 0;
  font-family: Inter, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #E7F6FF;
}

* {
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.noScroll {
  overflow: hidden;
}

.wrapper {
  max-width: 1080px;
  background: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  margin: 50px auto;
}


//Content styles

.content {

  img {
    max-width: 100%;
  }
}

.content_cards {
  display: flex;
  column-gap: 40px;
  row-gap: 20px;
  flex-wrap: wrap;
}

//Order buttons group
.ordersButtons {
  button {
    padding: 10px 15px;
    background-color: #9DD558;
    border-radius: 10px;
    outline: none;
    border: none;
    color: whitesmoke;
    font-weight: bold;
    cursor: pointer;
    transition: transform 0.1s ease-out;

    &:disabled {
      background-color: #bebebe;
    }

    &:hover {
      transform: scale(1.01, 1.01);
    }
  }
}


//Green button

.green_button {
  width: 100%;
  height: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #9DD558;
  border-radius: 18px;
  border: 0;
  font-weight: 600;
  font-size: 16px;
  color: #FFFFFF;
  cursor: pointer;
  transition: background-color 0.1s ease-in-out;

  img {
    position: relative;
    left: 18%;
  }

  &:disabled {
    animation: button-loading 0.6s ease-in-out infinite;
    background-color: #bebebe;
  }

  &:hover {
    background-color: lighten(#9DD558, 5%);

    img {
      transform: translateX(5px);
    }
  }

  &:active {
    background-color: darken(#9DD558, 5%);
  }
}

@keyframes button-loading {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}

